<script>
import numberFormat from '@/helpers/numberFormat';
import ButtonCountProducts from '@/components/ButtonCountProducts.vue';
import config from '@/config';
import axios from 'axios';
import Loader from '@/components/Loader.vue';

export default {
  name: 'ProductPage',
  components: {
    Loader,
    ButtonCountProducts,
  },
  data() {
    return {
      productAmount: 1,
      productData: null,
      currentColor: '',
      isLoading: false,
      isErrorLoading: false,
      loadData: null,
      productLoad: false,
      productLoading: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    idPage() {
      return this.$route.params.id;
    },
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
    image() {
      return this.productData.image.file;
    },
  },
  methods: {
    getProduct() {
      this.isLoading = true;
      this.isErrorLoading = false;
      clearInterval(this.loadData);
      this.loadData = setTimeout(() => {
        axios.get(`${config}/api/products/${this.idPage}`)
          .then((prod) => {
            this.productData = prod.data;
          })
          .catch(() => {
            this.isErrorLoading = true;
          })
          .then(() => {
            this.isLoading = false;
          });
      }, 0);
    },
    addToCard() {
      this.productLoading = true;
      this.productLoad = false;

      const params = {
        productId: this.idPage,
        amount: this.productAmount,
      };
      this.$store.dispatch('addProductCard', params)
        .then(() => {
          this.productLoad = true;
          this.productLoading = false;
        });
    },
  },
  watch: {
    idPage: {
      handler() {
        this.getProduct();
      },
      immediate: true,
    },
  },
};
</script>

